import React, {useState,useEffect} from 'react'
import {RiBarChartHorizontalFill} from 'react-icons/ri'
import {animateScroll as scroll} from 'react-scroll'
import {
    Nav, 
    NavbarContainer, 
    NavLogo, 
    MobileIcon, 
    NavMenu, 
    NavItem, 
    NavLinks,
    NavBtn,
    NavBtnLink
} from './NavbarElements';

const Navbar = ({toggle}) => {
    const [scrollNav,setScrollNav] = useState(false)

    const changeNav = () =>{
        if(window.scrollY >= 80){
            setScrollNav(true)
        }else{
            setScrollNav(false)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll',changeNav)
    }, [])

    const toggleHome=()=>{
        scroll.scrollToTop();
    }

    return (
        <>
            <Nav scrollNav={scrollNav}>
                <NavbarContainer>
                    <NavLogo to='/' onClick={toggleHome}>Al-<span style={{color: '#01bf71'}}> Aqwa</span></NavLogo>
                    <MobileIcon onClick={toggle}>
                        <RiBarChartHorizontalFill />
                    </MobileIcon>
                    <NavMenu>
                        <NavItem><NavLinks to="about" smooth={true} duration={500} spy={true} exact='true' offset={-80}>Tentang</NavLinks></NavItem>
                        <NavItem><NavLinks to="services" smooth={true} duration={500} spy={true} exact='true' offset={-80}>Layanan</NavLinks></NavItem>
                        <NavItem><NavLinks to="discover" smooth={true} duration={500} spy={true} exact='true' offset={-80}>Promo</NavLinks></NavItem>
                        {/* <NavItem><NavLinks to="signup" smooth={true} duration={500} spy={true} exact='true' offset={-80}>Sign Up</NavLinks></NavItem> */}
                    </NavMenu>
                    <NavBtn>
                        <NavBtnLink href="https://wa.me/+6285854627196" target="_blank">Contact</NavBtnLink>
                    </NavBtn>
                </NavbarContainer>
            </Nav>
        </>
    )
}

export default Navbar
