import React from 'react'
import Icon1 from '../../images/kambing/kambing-makan.webp';
import Icon2 from '../../images/kambing/kambing-hitam.png';
import Icon3 from '../../images/kambing/kambing-coklat.png';
import {
    ServicesContainer,
    ServicesH1,
    ServicesWrapper,
    ServicesCard,
    ServicesIcon,
    ServicesH2,
    ServicesP
} from './ServiceElements';

const Services = () => {
    return (
        <ServicesContainer id="services">
            <ServicesH1>Layanan Kami</ServicesH1>
                <ServicesWrapper>
                    <ServicesCard>  
                        <ServicesIcon src={Icon1} />
                        <ServicesH2>Kambing Sehat</ServicesH2>
                        <ServicesP>Kami menyediakan kambing yang sehat dan terawat. Makan tidak pernah telat.</ServicesP>
                    </ServicesCard>

                    <ServicesCard>  
                        <ServicesIcon src={Icon2} />
                        <ServicesH2>Kambing Beragam</ServicesH2>
                        <ServicesP>Kambing kami mempunyai jenis yang berbeda dari berat serta harganya.</ServicesP>
                    </ServicesCard>

                    <ServicesCard>  
                        <ServicesIcon src={Icon3} />
                        <ServicesH2>Kirim Keman Saja</ServicesH2>
                        <ServicesP>Kami bisa mengirimkan kambing kami ke seluruh pelosok negeri.</ServicesP>
                    </ServicesCard>
                </ServicesWrapper>
        </ServicesContainer>
    )
}

export default Services
