import React, {useState} from 'react';
import Video from "../../videos/kambing.mp4"
import {Button} from '../ButtonElements'
import {
    HeroContainer,
    HeroBg,
    VideoBg,
    HeroContent,
    HeroH1,
    HeroP,
    HeroBtnWrapper,
    ArrowForward,
    ArrowRight 
} from './HeroElements'

const HeroSection = () => {
    const [hover, setHover] = useState(false);

    const onHover = () => {
        setHover(!hover)
    }

    return (
        <HeroContainer id="home">
            <HeroBg>
                <VideoBg autoPlay loop muted src={Video} type='video/mp4' alt='Kambing Al-Aqwa' />
            </HeroBg>
            <HeroContent>
                <HeroH1>Kambing Terbaik Untuk Ibadah Terbaik</HeroH1>
                <HeroP>
                    Dapatkan kambing terbaik dari kami yang sudah berpengalaman menyediakan kambing untuk segala keperluan.
                </HeroP>
                <HeroBtnWrapper>
                    <Button 
                        to="about" 
                        onMouseEnter = { onHover } 
                        onMouseLeave = { onHover }
                        primary="true"
                        dark="true"
                        smooth={true} duration={500} spy={true} exact='true' offset={-80}
                    >
                        Mulai {hover ? <ArrowForward /> : <ArrowRight />}
                    </Button>
                </HeroBtnWrapper>
            </HeroContent>
        </HeroContainer>
    )
}

export default HeroSection;
